@import "./src/assets/themes/AppAddict/variables.scss";

.modal {
    padding: 15px;
}

.header {
    display: flex;
    gap: 10px;
    margin-bottom: 15px;

    &__info {
        display: flex;
        flex-direction: column;
        gap: 5px;
    }
}

.title {
    font-size: 16px;
    font-weight: 500;
    padding-right: 15px;
}

.support {
    position: relative;
    top: 2px;
    left: 1px;
}

.status {
    font-size: 14px;
    word-break: break-word;
}

.status,
.type {
    word-break: break-word;
}

.info {
    margin-top: 10px;

    td {
        padding-right: 10px;
        vertical-align: middle;

        &:first-child {
            font-weight: 500;
        }
    }

    tr:not(:last-child) {
        td {
            padding-bottom: 10px;
        }
    }
}

.expiration-date {
    display: inline-block;
    margin-right: 5px;
}
