@import "./src/assets/themes/AppAddict/variables.scss";

.icon {
    object-fit: cover;
    object-position: center;
    border-radius: 22.5%;
    mask: url(../assets/ios-mask.svg) center/100% 100% no-repeat;

    &_loading {
        opacity: 0;
    }

    &:not(&_filled) {
        background-color: var(--stub-bg);
    }
}
