@import "./src/assets/themes/AppAddict/variables.scss";

.tabs {
  min-width: 0;

  &__header {
    display: flex;
    overflow: auto;
    margin-bottom: 15px;

    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__trigger {
    font-size: 14px;
    font-family: var(--text-primary-font-family);
    color: var(--text-primary);
    font-weight: 500;
    padding: 5px 8px;
    border-bottom: 2px solid var(--tertiary-accent);
    transition: all .3s;

    &_active {
      border-bottom-color: var(--primary);
    }

    &_disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }
}
